:root {
  --bs-font-sans-serif: 'Rubik', sans-serif;
}
.container {
  max-width: 1530px;
}
body {
  background-color: rgb(243, 243, 243);
}
a {
  color: var(--bs-dark);
}

/* Category */
.category__link {
  text-align: center;
  display: block;
  text-decoration: none;
  transition: 250ms all;
}
.category__link:hover {
  transform: scale(1.1);
}
.category__image {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
  margin-bottom: 10px;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
  overflow: hidden;
  border-radius: 8px;
}
.category__image--large {
  height: 300px;
}
.category__image img {
  width: 100%;
  object-fit: cover;
}
.image_contact {
  width: 520px;
}
